        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M19 12H5" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
          <path d="M12 19L5 12L12 5" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
        </svg>
        
      </button>

      <!--  -->
      <div class="container">
        <h1 class="title">
           <span class="highlight-content">AI </span>
        </h1>
        <p class="subtitle">
           AI 
        </p>

        <!--  -->
        <div class="card" @click="handleCardClick('personalInfo', '')">
          <div class="block-title"></div>
          <div class="avatar-upload">
            <UploadableImage v-model="basicInfo.avatar" width="90" height="120"
              default-image="https://aicv-1307107697.cos.ap-guangzhou.myqcloud.com/asserts/icon/uploadAvatar.png" />
            <div class="upload-tip"> ()</div>
          </div>
          <div class="form-line">
            <AppleStyleInput id="name" labelText="" inputType="text" :required="true" v-model="basicInfo.name" />
            <AppleStyleInput id="phone" labelText=" ()" inputType="tel" v-model="basicInfo.phone" />
          </div>

          <AppleStyleInput id="email" labelText=" ()" inputType="email" v-model="basicInfo.email" />

          <div class="form-line">
            <AppleStyleInput id="targetCompany" labelText=" ()" inputType="text"
              v-model="basicInfo.targetCompany" />
            <AppleStyleInput id="jobTitle" labelText=" ()" inputType="text" v-model="basicInfo.jobTitle" />
          </div>
          <AppleStyleInput id="jobDescription" labelText=" ()" inputType="text"
            v-model="basicInfo.jobDescription" />
        </div>

        <!--  -->
        <div class="block-title"></div>
        <div id="education-experience" class="education-list">
          <div class="card" v-for="(edu, index) in educationList" :key="index"
            @click="handleCardClick('educationExperience', edu.school)">
            <div class="card-header">
              <div class="card-title">{{ index + 1 }}</div>
              <button class="remove-btn" type="button" @click.stop="removeCard('educationList', index)">
                
              </button>
            </div>

            <AppleStyleInput :id="`school-${index}`" labelText="" inputType="text" :required="true"
              v-model="edu.school" />

            <div class="form-line">
              <AppleStyleInput :id="`edu-time-${index}`" labelText="" inputType="text" :required="true"
                v-model="edu.time" />
              <AppleStyleInput :id="`major-${index}`" labelText="" inputType="text" :required="true"
                v-model="edu.major" />
            </div>

            <div class="form-line">
              <AppleStyleInput :id="`degree-${index}`" labelText="" inputType="text" :required="true"
                v-model="edu.degree" />
              <AppleStyleInput :id="`gpa-${index}`" labelText="GPA ()" inputType="text" v-model="edu.gpa" />
              <AppleStyleInput :id="`edu-city-${index}`" labelText="" inputType="text" :required="true"
                v-model="edu.city" />
            </div>
            <AppleStyleInput :id="`honors-${index}`" labelText=" ()" inputType="text" v-model="edu.honors" />
            <AppleStyleInput :id="`courses-${index}`" labelText=" ()" inputType="text" v-model="edu.courses" />
          </div>
        </div>
        <div>
          <button class="add-button" type="button" @click="addEducationExperience">
            + 
          </button>
        </div>

        <!--  -->
        <div class="block-title"></div>
        <div id="work-experience" class="experience-list">
          <div class="card" v-for="(work, index) in workList" :key="index"
            @click="handleCardClick('workExperience', work.company)">
            <div class="card-header">
              <div class="card-title">{{ index + 1 }}</div>
              <button class="remove-btn" type="button" @click.stop="removeCard('workList', index)">
                
              </button>
            </div>

            <AppleStyleInput :id="`company-${index}`" labelText="" inputType="text" :required="true"
              v-model="work.company" />

            <div class="form-line">
              <AppleStyleInput :id="`work-time-${index}`" labelText="" inputType="text" :required="true"
                v-model="work.time" />
              <AppleStyleInput :id="`title-${index}`" labelText="" inputType="text" :required="true"
                v-model="work.title" />
              <AppleStyleInput :id="`work-city-${index}`" labelText="" inputType="text" :required="true"
                v-model="work.city" />
            </div>
          </div>
        </div>
        <div>
          <button class="add-button" type="button" @click="addWorkExperience">
            + 
          </button>
        </div>

        <!--  -->
        <div class="block-title"></div>
        <div id="project-experience" class="project-list">
          <div class="card" v-for="(proj, index) in projectList" :key="index"
            @click="handleCardClick('projectExperience', proj.projectName)">
            <div class="card-header">
              <div class="card-title">{{ index + 1 }}</div>
              <button class="remove-btn" type="button" @click.stop="removeCard('projectList', index)">
                
              </button>
            </div>

            <AppleStyleInput :id="`project-${index}`" labelText="" inputType="text" :required="true"
              v-model="proj.projectName" />

            <div class="form-line">
              <AppleStyleInput :id="`project-time-${index}`" labelText="" inputType="text" :required="true"
                v-model="proj.time" />
              <AppleStyleInput :id="`role-${index}`" labelText="/" inputType="text" :required="true"
                v-model="proj.role" />
            </div>
          </div>
        </div>
        <div>
          <button class="add-button" type="button" @click="addProjectExperience">
            + 
          </button>
        </div>

        <!--  -->
        <button :disabled="isCreatingResume" class="submit-btn" type="button" @click="handleSubmit">
          {{ isCreatingResume ? '...' : '' }}
        </button>
      </div>
    </div>

    <!--  -->
    <div class="right-container cv-container" v-if="!isMobile">
      <component
        class="resume-container"
        :is="currentTemplateComponent"
        :color="color"
        :isPreview="true"
        :previewData="{
          personalInfo: basicInfo,
          education: mappedEducationList,
          workExperience: mappedWorkList,
          projectExperience: mappedProjectList,
        }"
      />
    </div>
  </div>
</template>

<script>
/**
 * ========== 1.  ========== 
 */
import AppleStyleInput from '@/components/basic_ui/AppleStyleInput.vue'
import UploadableImage from '@/components/basic_ui/UploadableImage.vue'
import DefaultCV from '@/components/template_ui/default/DefaultCVComponent.vue';
import GeneralSimpleCV from '@/components/template_ui/general_simple/GeneralSimpleCVComponent.vue';
import CreativeModernCV from '@/components/template_ui/creative_modern/CreativeModernCV.vue';

/**
 * ========== 3.  ========== 
 */
import { useToast } from 'vue-toastification'
import metadataInstance from '@/models/metadata_model.js'
import ChatgptModel from '@/models/chatgpt_model.js'
import { resumeModel } from '@/models/resume_model.js'
import apiClient from '@/api/axios'

const chatgptInstance = ChatgptModel.getInstance()

export default {
  name: 'UnifiedResumeForm',
  components: {
    AppleStyleInput,
    UploadableImage,
  },
  props: {
    /**
     * templateType
     *  'default' | 'general_simple' | 'creative_modern'
     */
    templateType: {
      type: String,
      default: 'default'
    },
    /**
     * color: 
     */
    color: {
      type: String,
      default: ''
    }
  },
  setup() {
    const toast = useToast()
    return { toast }
  },
  data() {
    return {
      basicInfo: {
        name: 'Tim',
        avatar: '',
        phone: '13800000000',
        email: 'tim@example.com',
        targetCompany: '',
        jobTitle: '',
        jobDescription: ''
      },
      educationList: [
        {
          school: '',
          time: '2018.09 - 2022.06',
          major: '',
          degree: '',
          gpa: '3.9',
          city: '',
          honors: '',
          courses: ', , '
        }
      ],
      workList: [
        {
          company: '',
          time: '2020.07 - 2023.01',
          title: '',
          city: ''
        }
      ],
      projectList: [
        {
          projectName: '',
          time: '2021.02 - 2021.04',
          role: ''
        }
      ],
      personalSummary: '',
      selectedModule: {
        type: '',
        title: ''
      }
    }
  },
  computed: {
    // 
    isCreatingResume() {
      return resumeModel.isFetching
    },
    currentTemplateComponent() {
      if (this.templateType == 'default') {
        return DefaultCV;
      } else if (this.templateType == 'general_simple') {
        return GeneralSimpleCV;
      } else if (this.templateType == 'creative_modern') {
        return CreativeModernCV;
      }
      return DefaultCV;
    },
    //  educationList 
    mappedEducationList() {
      return this.educationList.map((edu) => {
        const [from_time, to_time] = edu.time.split(' - ')
        return {
          title: edu.school,
          content: {
            major: edu.major,
            degree: edu.degree,
            gpa: edu.gpa,
            city: edu.city,
            honors: edu.honors,
            courses: edu.courses,
            from_time: from_time ? from_time.trim() : '',
            to_time: to_time ? to_time.trim() : '',
            content: []
          }
        }
      })
    },
    //  workList 
    mappedWorkList() {
      return this.workList.map((work) => {
        const [from_time, to_time] = work.time.split(' - ')
        return {
          title: work.company,
          content: {
            from_time: from_time ? from_time.trim() : '',
            to_time: to_time ? to_time.trim() : '',
            title: work.company,
            sub_title: work.title,
            city: work.city,
            content: []
          }
        }
      })
    },
    //  projectList 
    mappedProjectList() {
      return this.projectList.map((proj) => {
        const [from_time, to_time] = proj.time.split(' - ')
        return {
          title: proj.projectName,
          content: {
            from_time: from_time ? from_time.trim() : '',
            to_time: to_time ? to_time.trim() : '',
            content: []
          }
        }
      })
    }
  },
  methods: {
    /**
     *  selectedModule.type 
     *  templateType  Education/Work/Project/PersonalInfo 
     */
    getPreviewComponent(type) {
      // 
      const templatesMap = {
        default: {
          educationExperience: 'EducationSection',
          workExperience: 'WorkSection',
          projectExperience: 'ProjectSection',
          personalInfo: 'PersonalInfo',
          personalSummary: 'SummarySection'
        },
        general_simple: {
          educationExperience: 'EducationGeneralSimpleSection',
          workExperience: 'WorkGeneralSimpleSection',
          projectExperience: 'ProjectGeneralSimpleSection',
          personalInfo: 'PersonalGeneralSimpleInfo',
          personalSummary: 'SummaryGeneralSimpleSection'
        },
        creative_modern: {
          educationExperience: 'CreativeModernEducationSection',
          workExperience: 'CreativeModernWorkSection',
          projectExperience: 'CreativeModernProjectSection',
          personalInfo: 'CreativeModernPersonalInfo',
          personalSummary: 'CreativeModernSummarySection'
        }
      }

      const mapForCurrentTemplate = templatesMap[this.templateType] || templatesMap.default
      return mapForCurrentTemplate[type] || null
    },

    /**
     * 
     */
    handleSubmit() {
      // 
      const { name, phone, email, targetCompany, jobTitle, jobDescription, avatar } = this.basicInfo
      const educationList = this.educationList
      const workList = this.workList
      const projectList = this.projectList

      // 
      resumeModel.isFetching = true

      //  templateType  color
      apiClient
        .post('/user/resumes', {
          templateType: this.templateType,
          color: this.color
        })
        .then((response) => {
          const newResume = response.data.data

          // 
          metadataInstance.clearMetadata()
          chatgptInstance.clearConversations()

          //  ID
          resumeModel.setCurrentResumeId(newResume.resumeId)

          //  metadata
          metadataInstance.setContentForType('personalInfo', {
            name,
            phone,
            email,
            targetCompany,
            jobTitle,
            jobDescription,
            avatar
          })

          // 
          educationList.forEach((edu) => {
            const [fromTime, toTime] = edu.time.split(' - ')
            metadataInstance.setContentForType(
              'education',
              {
                title: edu.school,
                major: edu.major,
                degree: edu.degree,
                gpa: edu.gpa,
                city: edu.city,
                honors: edu.honors,
                courses: edu.courses,
                from_time: fromTime ? fromTime.trim() : '',
                to_time: toTime ? toTime.trim() : '',
                content: []
              },
              edu.school
            )
          })

          // 
          workList.forEach((work) => {
            const [fromTime, toTime] = work.time.split(' - ')
            metadataInstance.setContentForType(
              'workExperience',
              {
                title: work.company,
                sub_title: work.title,
                city: work.city,
                from_time: fromTime ? fromTime.trim() : '',
                to_time: toTime ? toTime.trim() : '',
                content: []
              },
              work.company
            )
          })

          // 
          projectList.forEach((proj) => {
            const [fromTime, toTime] = proj.time.split(' - ')
            const title = `${proj.projectName}`
            metadataInstance.setContentForType(
              'projectExperience',
              {
                title,
                from_time: fromTime ? fromTime.trim() : '',
                to_time: toTime ? toTime.trim() : '',
                content: []
              },
              title
            )
          })

          //  (CreateResume)
          this.$router.push({
            name: 'CreateResume',
            params: {
              templateType: this.templateType,
              resumeId: newResume.resumeId,
              color: this.color
            }
          })

          this.toast.success('')
        })
        .catch((error) => {
          console.error(':', error)
          const errorMsg = error.response?.data?.message || ''
          this.toast.error(errorMsg)
        })
        .finally(() => {
          resumeModel.isFetching = false
        })
    },

    // 
    goBack() {
      this.$router.go(-1)
    },

    // 
    addEducationExperience() {
      this.educationList.push({
        school: '',
        time: '',
        major: '',
        degree: '',
        gpa: '',
        city: ''
      })
    },
    // 
    addWorkExperience() {
      this.workList.push({
        company: '',
        time: '',
        title: '',
        city: ''
      })
    },
    // 
    addProjectExperience() {
      this.projectList.push({
        projectName: '',
        time: '',
        role: ''
      })
    },
    // 
    removeCard(listName, index) {
      this[listName].splice(index, 1)
    },
    /**
     * 
     */
    handleCardClick(type, name) {
      this.selectedModule = { type, title: name }
    }
  }
}
</script>

